import React from 'react'
import {AnimationExample} from './PtsExamples';
import './style.scss'
import ScrollAnimation from 'react-animate-on-scroll';


class Masthead extends React.Component {

  render() {


    return(
      <div className="masthead">
        <div className="mast-ani mt-2">
          <AnimationExample name="pts_anim" background="#000"/>
        </div>
        <div className="container-fluid">
          <div className="row">
            <div className="col-lg-6 col-md-10 col-sm-12 offset-lg-3  mt-5">
              <p className="slogan">
              I design things
              </p>
              <div className="row">
                <ScrollAnimation animateIn='fadeInUp'>
                  <div className="col">
                    <span className="slogan-hl">for interface</span>
                  </div>
                </ScrollAnimation>
                <ScrollAnimation animateIn='fadeInUp' delay='450'>
                  <div className="col">
                    <span className="slogan-hl">for empowerment</span>
                  </div>
                </ScrollAnimation>
                <ScrollAnimation animateIn='fadeInUp' delay='900'>
                  <div className="col">
                    <span className="slogan-hl">for democracy</span>
                  </div>
                </ScrollAnimation>
              </div> 
            </div>
          </div>
        </div>
      </div>
    )
}
}



export default Masthead
